* {
  box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body {
  background: var(--background);
  color: var(--foreground);
  font-family: var(--font-sans);
  line-height: 1.6;
  min-height: 100vh;
}
*:focus-visible {
  outline: 2px solid var(--lyncs-blue);
  outline-offset: 2px;
}
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
::-webkit-scrollbar-track {
  background: var(--lyncs-gray-100);
  border-radius: var(--radius-full);
}
::-webkit-scrollbar-thumb {
  background: var(--lyncs-gray-300);
  border-radius: var(--radius-full);
}
::-webkit-scrollbar-thumb:hover {
  background: var(--lyncs-gray-400);
}
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes slideInLeft {
  from {
    opacity: 0;
    transform: translateX(-20px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes slideInRight {
  from {
    opacity: 0;
    transform: translateX(20px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes pulse-soft {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
}
@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.animate-fade-in {
  animation: fadeIn 0.4s ease-out forwards;
}
.animate-slide-in-left {
  animation: slideInLeft 0.4s ease-out forwards;
}
.animate-slide-in-right {
  animation: slideInRight 0.4s ease-out forwards;
}
.animate-pulse-soft {
  animation: pulse-soft 2s ease-in-out infinite;
}
.animate-spin {
  animation: spin 1s linear infinite;
}
.gradient-purple {
  background: linear-gradient(135deg, var(--lyncs-purple-gradient-start) 0%, var(--lyncs-purple-gradient-end) 100%);
}
@keyframes marquee {
  0% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(-50%);
  }
}
.animate-marquee {
  animation: marquee 20s linear infinite;
}
@keyframes marquee-slide {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(-100%);
  }
}
.animate-marquee-slide {
  animation: marquee-slide 15s linear infinite;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}
.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
